.message {
    width: 50%;
    height: 100%;
    border-radius: 10px;
    border: 1px solid #ddd;
    outline: none;
    padding: 1rem;
    margin-left: 0.5rem;
  }
  .sendBtn {
    width: 7rem;
    height: 100%;
    background-color: green;
    padding: 0.9rem;
    padding-right: 1rem;
    margin-left: -5rem;
    border-radius: 10px;
    border: none;
    outline: none;
    color: #eae3d2;
    cursor: pointer;
  }

  .sendBtn:hover{
    background-color: rgb(8, 179, 8);
  }
.logo {
    width: 12rem;
    height: 4rem;
}

.logo-align {
    width:12rem;
    height: 4rem;
} 


.header {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 2rem;
    padding-right: 2rem;
    background-color: white;
    box-shadow: 1px 5px 1px white;
    width: 100%;
    height: 90px;
    z-index: 1000;
}

.right-items {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.btn {
    width: 4rem;
    height: 4rem;
}

.selection:hover {
    color: blue;
}
.banner {
    position: relative;
    top: 90px;
    z-index: 1;
    width: 100%;
    height: 12.5rem;
}

.img-background {
    position: absolute;
    display: flex;
    width: 100%;
    height: 600px;
    /* background-color: #FCD3AB; */
    background-image: linear-gradient(to bottom, #ff8621, #F6F3F3);

}

.img-text {
    color: white;
    padding: 4rem;
    margin-left: 3rem;
    width: 70%;
}

.slider {
    height: 700px;
    z-index: -10;
}

.Div {
    position: relative;
    padding-top: 4rem;
    margin-left: 10rem;
    font-size: 1rem;
    width: 30%;
}

.textStyle {
    color: white;
    font-family: 'Times New Roman', Times, serif
}
.icon{
    color:red
}

.icon:hover{
    color:yellow
}
.like_btn1 {
    position: absolute;
    top: 1.5rem;
    left: 11.4rem;
    z-index: 100;
}

.like_btn2 {
    position: absolute;
    top: 1.5rem;
    left: 11.4rem;
    z-index: 99;
    color: #EEEBEB;
}

.boxitems {
    position: relative;
    z-index: 10;
}

.box{
    border: 5px;
    background-color: white;
}
